<script setup>
import { ref, onMounted, computed } from 'vue'
import { 
  Trophy, FileText, Clock, LogIn, LogOut, Edit, PlusCircle, TrendingUp, 
  CheckCircle, User, UserCircle, Download, Eye, PieChart, BarChart3 
} from 'lucide-vue-next'
import { useAuthStore } from '../stores/auth'
import { useRouter, useRoute } from 'vue-router'

const router = useRouter()
const route = useRoute()
const authStore = useAuthStore()
const activeTab = ref('Overview')
const isLoading = ref(true)

const dashboardData = ref({
    stats: {
        total_bids: 0,
        orders_won: 0,
        pending_review: 0,
        win_rate: '0%',
        total_bid_value: "0.00",
        orders_won_value: "0.00",
    },
    profile: null,
    recent_bids: [],
    recent_activity: [], 
    upcoming_deadlines: [],
    supplier_name: '',
    user_name: '',
    isSessionExpired: false
})
const profileCompletion = computed(() => {
    const p = dashboardData.value?.profile;
    if (!p) return 0;
    
    const primaryAddr = p.__onload?.addr_list?.[0] || {};
    const primaryContact = p.__onload?.contact_list?.[0] || {};

    const fields = [
        p.supplier_name,              // 1. Company Name (Found)
        p.supplier_type,              // 2. Business Type (Found)
        p.gstin,                      // 3. GST (Empty -> This is the missing 20%)
        primaryContact.email_id,      // 4. Email (Nested in __onload)
        primaryAddr.address_line1     // 5. Address (Nested in __onload)
    ];

    const filled = fields.filter(f => f && f !== '' && f !== 'Not Specified').length;
    
    return Math.round((filled / fields.length) * 100);
});

const bidHistory = computed(() => {
    return (dashboardData.value.recent_bids || []).map(bid => ({
        id: bid.id,             
        title: bid.title,       
        amount: bid.amount,     
        submitted: bid.date,    
        rank: bid.rank || '-',  
        status: bid.status,     
        statusColor: bid.statusColor 
    }))
})

const stats = ref([
  { name: 'Total Bids', value: '0', icon: FileText },
  { name: 'Orders Won', value: '0', icon: Trophy },
  { name: 'Pending Review', value: '0', icon: Clock },
  { name: 'Win Rate', value: '0%', icon: TrendingUp },
])

const formatDate = (dateStr) => {
    if (!dateStr) return 'TBD';
    const date = new Date(dateStr);
    return date.toLocaleDateString('en-IN', {
        day: '2-digit',
        month: 'short',
        year: 'numeric'
    });
};

const fetchDashboardStats = async () => {
    try {
        const [statsRes, profileRes] = await Promise.all([
            authStore.secureFetch('/api/method/supplier_portal.api.get_dashboard_stats'),
            authStore.secureFetch('/api/method/supplier_portal.api.get_supplier_profile')
        ]);

        if (!statsRes.ok && (statsRes.status === 403 || statsRes.status === 401)) {
           dashboardData.value.isSessionExpired = true;
           return;
        }

        const statsResult = await statsRes.json();
        const profileResult = await profileRes.json();

        if (statsResult.message) {
            dashboardData.value = {
               ...statsResult.message,
               profile: profileResult.message || null,
               upcoming_deadlines: statsResult.message.upcoming_deadlines || []
            };
            
            const s = statsResult.message.stats;
            
            stats.value[0].value = s.total_bids || 0;
            stats.value[1].value = s.orders_won || 0;
            stats.value[2].value = s.pending_review || 0;
            stats.value[3].value = s.win_rate || '0%';

            console.log("Dashboard Data Synced with Profile Info");
        }
    } catch (e) {
        console.error("Dashboard Sync Error:", e);
    } finally {
        isLoading.value = false;
    }
};

const getActivityStyle = (operation) => {
    const map = {
        'Login': { icon: LogIn, color: 'bg-blue-50 text-blue-600' },
        'Logout': { icon: LogOut, color: 'bg-gray-100 text-gray-600' },
        'Update': { icon: Edit, color: 'bg-amber-50 text-amber-600' },
        'Post': { icon: PlusCircle, color: 'bg-green-50 text-green-600' },
        'Submit': { icon: CheckCircle, color: 'bg-indigo-50 text-indigo-600' }
    }
    return map[operation] || { icon: Clock, color: 'bg-gray-50 text-gray-600' }
}

const processedActivities = computed(() => {
    return (dashboardData.value.recent_activity || []).map(act => ({
        title: act.title,
        time: act.time, 
        ...getActivityStyle(act.icon_type) 
    }))
})

onMounted(async () => {
    isLoading.value = true;
    try {
        if (!authStore.isAuthenticated) {
            await authStore.initializeAuth();
        }
        
        if (!authStore.isAuthenticated) {
            window.location.href = "/app"; 
            return;
        }

        await fetchDashboardStats();
    } catch (error) {
        console.error("Mounting Error:", error);
    } finally {
        isLoading.value = false;
    }
});
</script>

